<template>
  <div class="dance">
    <List :listData="listData"/>
  </div>
</template>

<script>
import List from './List.vue'
export default{
  components:{
    List,
  },
  data(){
    return{
      listData:{}
    }
  },
  async created() {
    const {data:res}=await this.$http.get('../dance.json');
    this.listData=res;

  }
 }
</script>

